import api from '../api/main'
import {
    ADD_TO_CONVERSATION,
    GET_CHAT,
    GET_CONVERSATIONS, SET_CONVERSATIONS, STATUS_CLEAN, STATUS_ERROR, STATUS_REQUEST,
    STATUS_SUCCESS
} from "../mutation-types";
const state = {
    status: 'clear',
    list: [],
}

const getters = {
    [GET_CONVERSATIONS]: (state) => state.list.slice()
}

const mutations = {
    [STATUS_REQUEST]: state => {
        state.status = 'loading'
    },
    [STATUS_SUCCESS]: state => {
        state.status = 'success'
    },
    [STATUS_ERROR]: state => {
        state.status = 'error'
    },
    [STATUS_CLEAN]: state => {
        state.status = 'clean'
    },
    [SET_CONVERSATIONS]: (state, payload) => {
        state.list = payload.slice()
    },
    [ADD_TO_CONVERSATION]: (state, {chatId, message}) => {
        const index = state.list.findIndex(c => c._id === chatId)
        state.list[index].lastMessage = {...state.list[index].lastMessage, text: message};
    },
}

const actions = {
    [GET_CONVERSATIONS]: ({commit}, userType) => {
        commit(STATUS_REQUEST)
        api.get(`${userType}/chat/conversations`).then(res => {
            commit(SET_CONVERSATIONS, res.data.conversations)
            commit(STATUS_SUCCESS)
        }).catch(err => {
            console.log(err)
            commit(STATUS_ERROR)
        })
    },
    [GET_CHAT]: ({commit}, userType) => {
        commit(STATUS_REQUEST)
        api.get(`${userType}/chat/conversations`).then(res => {
            commit(SET_CONVERSATIONS, res.data.conversations)
            commit(STATUS_SUCCESS)
        }).catch(err => {
            console.log(err)
            commit(STATUS_ERROR)
        })
    },
    [ADD_TO_CONVERSATION]: ({commit}, {chatId, message}) => {
        commit(ADD_TO_CONVERSATION, {chatId, message})
    }
}

export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions
}