<template>
    <div class="filter-vacancy">
        <div class="d-flex mb-5 align-items-center">
            <div class="a-ui mr-3" style="width: 100%;">
                <el-input placeholder="Сфера деятельности" style="width: 100%" v-model="filters.vacancyField"></el-input>
            </div>
            <div class="" style="min-width: 100px">
                <el-button @click="onFilter()" size="default">Поиск</el-button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'filter-vacancy',
        data() {
            return {
                filters: {
                    vacancyField: '',
                    type: [],
                    minSalary: 0,
                    maxSalary: 300000
                },
                vacancyFields: {
                    value:''
                },
                city: {
                    options: [{
                        value: 'Астана',
                        label: 'Астана'
                    }, {
                        value: 'Алматы',
                        label: 'Алматы'
                    }, {
                        value: 'Актобе',
                        label: 'Актобе'
                    }, {
                        value: 'Актау',
                        label: 'Актау'
                    }, {
                        value: 'Атырау',
                        label: 'Атырау'
                    }, {
                        value: 'Караганды',
                        label: 'Караганды'
                    }, {
                        value: 'Шымкент',
                        label: 'Шымкент'
                    }],
                    value: ''
                },
                employment: {
                    options: [
                        {label: 'Полная занятость', value: 'Полная занятость'},
                        {label: 'Частичная занятость', value: 'Частичная занятость'},
                        {label: 'Проектная работа', value: 'Проектная работа'},
                        {label: 'Стажировка', value: 'Стажировка'},
                        {label: 'Практика', value: 'Практика'}
                    ],
                    values: []
                },
                search: {
                    value: ''
                },
            }
        },
        methods: {
            onFilter() {
                const filters = {
                    minSalary: this.filters.minSalary,
                    maxSalary: this.filters.maxSalary,
                }
                if (this.filters.vacancyField !== '') {
                    filters.vacancyField = this.filters.vacancyField
                }
                if (this.filters.type.length > 0) {
                    filters.type = this.filters.type
                }
                this.$emit('filter', filters)
            }
        }
    }
</script>