import api, {baseURL} from '../api/main'
import {
    GET_PROFILE, MERGE_PROFILE, PROFILE_CLEAN, PROFILE_ERROR, PROFILE_REQUEST,
    PROFILE_SUCCESS
} from '../types/companyProfile';
import {logoutActions} from "../../actions/auth";
import {GET_STATUS, RESEND_VERIFICATION} from "../mutation-types";
const state = {
    status: 'clean',
    profile: {
        id: null,
        name: null,
        phone: null,
        email: null,
        image: null,
        description: null,
        confirmed: false
    }
}

const getters = {
    [GET_PROFILE]: state => state.profile,
    [GET_STATUS]: state => state.status,
}

const mutations = {
    [MERGE_PROFILE]: (state, profile) => {
        profile.id = profile._id
        delete profile._id
        profile.image = baseURL + '/company/image-avatar/' + profile.id + '.png'
        state.profile = profile
    },
    [PROFILE_REQUEST]: state => {
        state.status = 'loading'
    },
    [PROFILE_SUCCESS]: state => {
        state.status = 'success'
    },
    [PROFILE_ERROR]: state => {
        state.status = 'error'
    },
    [PROFILE_CLEAN]: state => {
        state.status = 'clean'
        state.profile = {
            id: null,
            name: null,
            phone: null,
            email: null,
            image: null,
            description: null
        }
    }
}

const actions = {
    [GET_PROFILE]: ({commit}) => new Promise(() => {
        commit(PROFILE_REQUEST)
        api.get('company/private/profile').then(res => {
            console.log(res)
            commit(PROFILE_SUCCESS)
            commit(MERGE_PROFILE, JSON.parse(JSON.stringify(res.data)))
        }).catch(err => {
            if (err.response && (err.response.status === 401 || err.response.status === 403)) {
                logoutActions()
            }
            commit(PROFILE_ERROR)
            console.log(err)
        })
    }),
    [RESEND_VERIFICATION]: () => api.get('company/auth/resend-verification')
}


export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions
}