<template>
    <div class="chat-container" v-loading="requestStatus === 'loading'">
        <beautiful-chat
                v-if="chat"
                :participants="participants"
                :titleImageUrl="titleImageUrl"
                :onMessageWasSent="onMessageWasSent"
                :messageList="renderMessages"
                :newMessagesCount="newMessagesCount"
                :close="onClose"
                :open="onOpen"
                :isOpen="true"
                :showEmoji="true"
                :showFile="true"
                :showTypingIndicator="''"
                :colors="colors"
                :alwaysScrollToBottom="alwaysScrollToBottom">
        </beautiful-chat>
        <div class="chat-conversation-placeholder d-flex align-items-center justify-content-center" v-if="!chat">
            <div class="text-center">
                <i class="fas fa-comments"></i>
                <p>Выберите чат</p>
            </div>
        </div>
    </div>
</template>

<script>

    import io from 'socket.io-client';
    import {AUTH, GET_TOKEN, GET_TYPE} from "../../store/types/auth";
    import {baseSocketURL, baseURL} from "../../store/api/main";
    import {
        ADD_TO_CONVERSATION,
        CHAT,
        CONVERSATIONS,
        GET_CHAT,
        GET_PROFILE_STORE,
        GET_STATUS
    } from "../../store/mutation-types";
    import {GET_PROFILE} from "../../store/types/companyProfile";
    export default {
        name: 'chat-conversation',
        data() {
            return {
                messageList: [
//                    { type: 'text', author: `me`, data: { text: `Say yes!` } },
//                    { type: 'text', author: `user1`, data: { text: `No.` } }
                ], // the list of the messages to show, can be paginated and adjusted dynamically
                newMessagesCount: 0,
                colors: {
                    header: {
                        bg: '#4e8cff',
                        text: '#ffffff'
                    },
                    launcher: {
                        bg: '#4e8cff'
                    },
                    messageList: {
                        bg: '#ffffff'
                    },
                    sentMessage: {
                        bg: '#4e8cff',
                        text: '#ffffff'
                    },
                    receivedMessage: {
                        bg: '#eaeaea',
                        text: '#222222'
                    },
                    userInput: {
                        bg: '#f4f7f9',
                        text: '#565867'
                    }
                },
                alwaysScrollToBottom: true, // when set to true always scrolls the chat to the bottom when new events are in (new message, user starts typing...)
                socket: io(baseSocketURL),
                socketAuthenticated: false,
            }
        },
        props: {
            chat: {
                chatImage: String,
                chatId: String,
                chatMessage: String,
                chatName: String,
                chatReceiver: String,
                unread: Boolean,
                lastMessageId: String
            }
        },
        methods: {
            sendMessage (text) {
                if (text.length > 0) {
                    this.newMessagesCount = this.isChatOpen ? this.newMessagesCount : this.newMessagesCount + 1
                    this.onMessageWasSent({ author: 'support', type: 'text', data: { text } })
                }
            },
            onMessageReceived(message) {
                this.messageList = [message, ...this.messageList]
                this.$store.dispatch(CONVERSATIONS + ADD_TO_CONVERSATION, {message: message.data.text, chatId: this.chat.chatId})
            },
            onMessageWasSent (message) {
                // called when the user sends a message
                const messageText = message.data.text
                this.socket.emit('chat_message', {
                         conversationId: this.chat.chatId,
                         messageType: 'text',
                         text: messageText,
                         timeSent: new Date(),
                }, () => {
                    this.messageList = [message, ...this.messageList]
                    this.$store.dispatch(CONVERSATIONS + ADD_TO_CONVERSATION, {message: message.data.text, chatId: this.chat.chatId})
                })
            },
            onOpen() {},
            onClose() {},
            getImageUrl(role, id) {
                return `${baseURL}/${role}/image-avatar/${id}.png`
            },
            loadMessages() {
                this.$store.dispatch(CHAT + GET_CHAT, {
                    userType: this.userRole,
                    chatId: this.chat.chatId,
                    lastMessage: '_'
                }).then(res => {
                    const messages = []
                    res.forEach(c => {
                        messages.push({
                            type: c.messageType,
                            author: this.userId === c.authorId ? 'me' : c.authorId,
                            data: {
                                text: c.text
                            }
                        })
                    });
                    this.messageList = messages
                })
            }
        },
        computed: {
            profileStore() { return this.$store.getters[AUTH + GET_PROFILE_STORE]},
            userRole() { return this.$store.getters[AUTH + GET_TYPE] },
            userId() { return this.$store.getters[this.profileStore + GET_PROFILE].id },
            requestStatus() {return this.$store.getters[CHAT + GET_STATUS]},
            participants() {
                return [{
                    id: this.chat ? this.chat.chatReceiver : '',
                    name: this.chat ? this.chat.chatReceiver : '',
                    imageUrl: this.chat ? this.chat.chatImage : '',
                }]
            },
            titleImageUrl() { return this.chat.chatImage},
            renderMessages() { return this.messageList.slice().reverse()}
        },
        mounted() {
            console.log(this.socket)
            this.socket.on('connect', () => {
                this.socket.emit('authentication', {
                    token: this.$store.getters[AUTH + GET_TOKEN],
                    userType: this.$store.getters[AUTH + GET_TOKEN]
                });
                this.socket.on('authenticated', () => {
                    this.socketAuthenticated = true;
                });
                this.socket.on('chat_message', (data) => {
                    console.log('in', data)
                    if (data.authorId !== this.chat.chatReceiver) return
                    const message = {
                        type: data.messageType,
                        author: data.authorId,
                        data: {
                            text: data.text
                        }
                    }
                    this.onMessageReceived(message)
                });
            })
            if (this.requestStatus === 'clear') {
                this.messageList = []
                this.loadMessages()
            }
        },
        watch: {
            chat: function(to, from) {
                if (to && (!from || to.chatId !== from.chatId)) {
                    this.messageList = []
                    this.loadMessages()
                }
            }
        }
    }
</script>

